<template>
  <div class="input-number">
    <fx-card title="基础用法">
      <fx-input-number v-model="baseInputNumber"></fx-input-number>
      <template #hide>
        <pre v-highlight="base"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="限制最大最小值 min=5 max=10">
      <fx-input-number v-model="inputNumber" :min="5" :max="10"></fx-input-number>
      <template #hide>
        <pre v-highlight="number"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="禁用状态">
      <fx-input-number v-model="disabledInputNumber" disabled></fx-input-number>
      <template #hide>
        <pre v-highlight="disabled"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="步数">
      <fx-input-number v-model="stepInputNumber" :step="3"></fx-input-number>
      <template #hide>
        <pre v-highlight="step"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="按钮位置">
      <fx-input-number v-model="rightInputNumber" right></fx-input-number>
      <template #hide>
        <pre v-highlight="right"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="大小">
      <fx-input-number v-model="sizeInputNumber" size="large"></fx-input-number>
      <fx-input-number v-model="sizeInputNumber" size="middle"></fx-input-number>
      <fx-input-number v-model="sizeInputNumber" size="default"></fx-input-number>
      <fx-input-number v-model="sizeInputNumber" size="mini"></fx-input-number>
      <template #hide>
        <pre v-highlight="size"><code></code></pre>
      </template>
    </fx-card>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'inputNumber',
  setup () {
    const baseInputNumber = ref<number>(1)
    const inputNumber = ref<number>(6)
    const disabledInputNumber = ref<number>(1)
    const stepInputNumber = ref<number>(1)
    const rightInputNumber = ref<number>(1)
    const sizeInputNumber = ref<number>(1)

    const base = '<fx-input-number v-model="baseInputNumber"></fx-input-number>'
    const number = '<fx-input-number v-model="inputNumber" :min="5" :max="10"></fx-input-number>'
    const disabled = '<fx-input-number v-model="disabledInputNumber" disabled></fx-input-number>'
    const step = '<fx-input-number v-model="stepInputNumber" :step="3"></fx-input-number>'
    const right = '<fx-input-number v-model="rightInputNumber" right></fx-input-number>'
    const size = `<fx-input-number v-model="sizeInputNumber" size="large"></fx-input-number>
<fx-input-number v-model="sizeInputNumber" size="middle"></fx-input-number>
<fx-input-number v-model="sizeInputNumber" size="default"></fx-input-number>
<fx-input-number v-model="sizeInputNumber" size="mini"></fx-input-number>`

    return {
      baseInputNumber,
      disabledInputNumber,
      stepInputNumber,
      rightInputNumber,
      sizeInputNumber,
      inputNumber,
      base,
      disabled,
      step,
      right,
      size,
      number
    }
  }
})
</script>

<style scoped lang="scss">
.input-number {
}
</style>
